//	Headings
@import url("https://fonts.googleapis.com/css?family=Roboto:400,500,900");

.gigantic,
.huge,
.large,
.bigger,
.big,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #222;
  font-weight: bold;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.07);
}

.gigantic {
  font-size: 110px;
  line-height: 1.09;
  letter-spacing: -2px;
}

.huge,
h1 {
  font-size: 68px;
  line-height: 1.05;
  @include md {
    font-size: 44px;
  }
}

.large,
h2 {
  font-size: 42px;
  line-height: 1.14;
  @include md {
    font-size: 32px;
  }
}

.bigger,
h3 {
  font-size: 26px;
  line-height: 1.38;
  @include md {
    font-size: 22px;
  }
}

.big,
h4 {
  font-size: 22px;
  line-height: 1.38;
  @include md {
    font-size: 18px;
  }
}

.small,
small {
  font-size: 10px;
  line-height: 1.2;
}

// Basic Text Style
p {
  margin: 0 0 20px 0;
  font-weight: 400;
  font-size: 14px;
}

em {
  font-style: italic;
}

strong {
  font-weight: bold;
}

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0;
}
